.calendar-flex-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.calendar-flex-column {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.btn-select-day.active:hover, .btn-select-day:hover, .btn-select-day.current:hover,  .btn-select:hover  {
    cursor: pointer;
    border: 2px solid #adb0b2; 
}

.btn-select-day.active:hover {
    cursor: pointer;
    border: 2px solid #02d86d; 
}

.btn-select-day:active, .btn-select:active {
    /* border: 2px solid red; */
    background: #adb0b2;
}

a.btn-select-day {
    cursor: pointer;    
    -webkit-transition: all ease 0.3s;    
    -o-transition: all ease 0.3s;    
    transition: all ease 0.3s;
    border-radius: 50%;
    border: 2px solid transparent;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.no-current-month.btn-select-day {
    color: #90c4de;
}

a.btn-select {
    cursor: pointer;    
    -webkit-transition: all ease 0.3s;    
    -o-transition: all ease 0.3s;    
    transition: all ease 0.3s;
    border-radius: 16px;
    border: 2px solid transparent;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.btn-select-day.active {
    border: 2px solid #00b259; 
}
.btn-select-day.current {
    border: 2px solid #d0e8f5;
}

.icon-down-arrow {
    -webkit-transition:  0.3s;
    -o-transition:  0.3s;
    transition:  0.3s;
    fill: #013a81;
}

.icon-svg {
    fill: #013a81;
}

.icon-down-arrow.dropdown-active{
    -webkit-transform: rotate(-180deg);
        -ms-transform: rotate(-180deg);
            transform: rotate(-180deg);
    -webkit-transition:  0.3s;
    -o-transition:  0.3s;
    transition:  0.3s;
}

.select-month {
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.not-year {
    position: relative;
    /* transform: translateX(100%); */
    width: auto;
    max-width: 0;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    overflow: hidden;
}
.select-year {
    position: relative;
    right: 0;
    width: auto;
    max-width: 100%;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    overflow: hidden;
}

.on-select-day {
    height: auto;
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
    /* max-height: 100%; */
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.off-select-day {
    height: auto;
    -webkit-transform: translateY(0%);
        -ms-transform: translateY(0%);
            transform: translateY(0%);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.on-select-month {
    height: auto;
    -webkit-transform: translateY(0%);
        -ms-transform: translateY(0%);
            transform: translateY(0%);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.off-select-month {
    height: auto;
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
.modal-calendar-button {
    position: absolute;
    top: 100%;
    right: 0;
    opacity: .5;
    background: #FFFFFF;
    overflow: hidden;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: right top;
        -ms-transform-origin: right top;
            transform-origin: right top;
    padding: 8px 0px;
    margin: 0 10px;
    border-radius: 4px;
    -webkit-box-shadow: 0 8px 10px 1px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.3);
            box-shadow: 0 8px 10px 1px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.3);    
    z-index: 1003;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.modal-calendar-button.active, .modal-calendar-flex.active, .modal-calendar-overlay.active {
    opacity: 1;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);    
    -webkit-transition: .5s;    
    -o-transition: .5s;    
    transition: .5s;
}

.modal-calendar-flex {
    position: absolute;
    opacity: .5;
    background: #FFFFFF;
    overflow: hidden;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: center;
        -ms-transform-origin: center;
            transform-origin: center;
    padding: 8px 0px;
    margin: 0 10px;
    border-radius: 4px;
    -webkit-box-shadow: 0 8px 10px 1px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.3);
            box-shadow: 0 8px 10px 1px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.3);    
    z-index: 1003;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.modal-calendar-overlay {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;    
    -webkit-transform: scale(0);    
        -ms-transform: scale(0);    
            transform: scale(0);
    opacity: 0;
    height: 100%;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}



